.header{
    background-color: $grisOscuro;
    h1{
        color:#fff;
        padding-bottom: 5rem;
        text-align: left;
    }

}

.barra{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 2rem;
    
}

@media (min-width: 768px){
    .barra{
        flex-direction: row;
        justify-content: space-between;
        padding-top: 3rem;
    }
}

.navegacion{
    a{
        color: $blanco;
        text-decoration: none;
        font-size: 2rem;
        margin-right: 2rem;
    }
}

.inicio{
    background-image: url('/build/img/header.jpg');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

@media (min-width: 768px){
   .inicio{
    min-height: 50rem;
   }
}