@include tablet{
    .seccion-inferior{
        display: grid;
        grid-template-columns: 2fr 1fr;
        gap: 2rem;
    }
    .entrada-blog{
        margin-bottom: 2rem;
        @include tablet{
            display: grid;
            grid-template-columns: 1fr 2fr;
            gap: 2rem;
        }
    }
}

.texto-entrada{
    a{
        color: $negro;
    }
    h4{
        margin: 0;
        line-height: 1.4;
    }
    span{
        color: $amarillo;

    }
}

