.formulario{
    padding: 1rem 1rem;
    .field{
        width: 100%;
        margin: 1rem auto;
    }
    label{
        font-weight: 600;
    }
    legend{
        font-size: 2rem;
    }
    p{
        font-size:1.5rem;
        color: $grisOscuro;
    }
}

input:not([type='submit']){
    padding: 0.5rem 0;
    border: 1px solid $gris;
    border-radius: 1rem;
    padding-left: 1rem;;
}

fieldset{
    border: solid 1px;
}

input[type="radio"]{
    margin: 0 2rem;
}

input[type="radio"]{
    margin: 0 2rem;
}


select{
    background-color: $blanco;
    padding: 0.5rem;
    border: 1px solid $gris;
    border-radius: 1rem;
}

