@media (min-width: 768px) {
    .contenedor-anuncios{
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap: 2rem;
    }
}

.anuncio{
    background-color: lighten($gris,10);
    border: 1px solid $gris;

    .contenido-anuncio{
        padding: 2rem;
    }
    h3,p{
        margin: 0;
    }
    .precio{
        color: $verde;
        font-size: 2.4rem;
        font-weight: bold;
    }
}

.iconos-caracteristicas{
    display: flex;
    li{
        flex: 1;
        display: flex;
        img{
            flex:0 0 3rem;
            margin-right: 1rem;
        }
    }

}

ul{
    list-style-type: none;
    margin: 0;
}